import {Component, OnInit} from '@angular/core';
import {TableStructure} from 'src/app/own/data-table/tableStructure';
import {Observable} from 'rxjs';
import {Ticket} from 'src/app/models/ticket';
import {TicketsService} from 'src/app/services/tickets.service';
import {NbDialogService} from '@nebular/theme';
import {DialogComponent} from 'src/app/own/dialog/dialog.component';
import {CompleterService, CompleterData} from 'ng2-completer';

interface Producto {
  id?: number;
  descripcion?: string;
  iva?: number;
  costo?: number;
  subtotal?: number;
  cantidad: number;
}

interface Compra {
  fecha_solicitud?: string;
  fecha_entrega?: string;
  factura?: number;
  iva?: number;
  subtotal?: number;
  total?: number;
  aplicar?: boolean;
  productos?: Producto[];
  agente_id?: number;
}

@Component({
  selector: 'app-compras',
  templateUrl: './compras.component.html',
  styleUrls: ['./compras.component.scss']
})

export class ComprasComponent implements OnInit {
  public tableStructure: TableStructure = {
    columns: [
      {title: 'ID', name: 'id', editable: false, type: 'number', filter: false},
      {title: 'Aplicado', name: 'aplicado', editable: true, type: 'toggle', filter: false},
      // {title: 'Productos', name: 'productos', editable: false, type: 'button', filter: false},
      {title: 'Proveedor', name: 'agente_id', editable: true, type: 'list', filter: true},
      {title: 'No. factura', name: 'numero_factura', editable: true, type: 'number', filter: true},
      {title: 'Solicitud', name: 'fecha_solicitud', editable: true, type: 'date', filter: true},
      {title: 'Enterega', name: 'fecha_entrega', editable: true, type: 'date', filter: true},
      {title: 'Subtotal', name: 'subtotal', editable: true, type: 'number', filter: true},
      {title: 'Iva', name: 'iva', editable: true, type: 'number', filter: true},
      {title: 'Total', name: 'total', editable: true, type: 'number', filter: true},
    ],
    buttons: {
      create: false,
      delete: true,
      update: true
    },
  };

  public tickets: Observable<Ticket[]>;
  public errorsPerInput = [];
  public messageSuccess = '';
  public clickedCreat = false;
  public dataService: CompleterData;
  public productos;
  public compra: Compra;
  public listaProveedores: Observable<any>;
  public datatable;
  public inUpdateProducts = false;

  constructor(private ticketsService: TicketsService, private dialogService: NbDialogService, private completerService: CompleterService) {

  }

  ngOnInit() {
    this.tickets = this.ticketsService.getTicket();
  }

  public delete(deleteData, dataTable) {
    this.dialogService.open(DialogComponent, {
      context: {
        title: '¡Cuidado!',
        content: `Si elimina este registro, se perderá el historial de movimientos relacionados. ¿Desea continuar?`
      },
    }).onClose.subscribe(response => {
      if (response) {
        this.reset();
        dataTable.charginData++;
        // tslint:disable-next-line:no-shadowed-variable
        this.ticketsService.deleteTicket(deleteData).subscribe(response => {
          dataTable.charginData--;
          this.messageSuccess = 'Compra eliminada correctamente';
          dataTable.deleteData(response);
        }, error => {
          this.displayErrors(error);
        });
      }
    });
  }

  private reset() {
    this.errorsPerInput = [];
    this.messageSuccess = '';
    setTimeout(() => {
      this.errorsPerInput = [];
      this.messageSuccess = '';
    }, 10000);
  }

  private displayErrors(error, dataTable?) {
    dataTable ? dataTable.charginData-- : 0;
    window.scrollTo(0, 0);
    for (const key in error.error.errors) {
      let err: string = error.error.errors[key][0];
      if (err.includes('producto')) {
        if (err.includes('id')) {
          continue;
        }
        err = err.replace('.', ' ');
        err = err.replace('.', ' ');
      }
      this.errorsPerInput.push([err]);
    }
  }

  public setLists(datatable) {
    datatable.lists.agente_id = this.ticketsService.getNameAgentes();
    datatable.islists = true;
  }

  public clickCreate(datatable) {
    this.inUpdateProducts = false;
    this.datatable = datatable;
    this.clickedCreat = true;
    this.compra = {aplicar: false};
    this.compra.productos = [];
    if (!this.listaProveedores) {
      this.listaProveedores = this.ticketsService.getNameAgentes();
    }
    this.ticketsService.getProductos().subscribe(response => {
      this.productos = response;
      this.dataService = this.completerService.local(this.productos, 'descripcion', 'descripcion');
    });
  }

  public clickAgregarProducto() {
    this.compra.productos.push({cantidad: 1});
  }

  public selectedChange(event, i) {
    if (this.compra.productos[i]) {
      console.warn(event);
    }
  }

  public deleteProducto(i) {
    this.compra.iva -= this.compra.productos[i].iva ? this.compra.productos[i].iva : 0;
    this.compra.subtotal -= this.compra.productos[i].subtotal ? this.compra.productos[i].subtotal : 0;
    this.compra.total -= this.compra.productos[i].costo ? this.compra.productos[i].costo : 0;
    this.compra.productos.splice(i, 1);
  }

  public guardarCompra() {
    this.compra.productos.forEach(producto => {
      if (producto) {
        this.productos.forEach(productoStock => {
          if (producto.descripcion === productoStock.descripcion) {
            producto.id = productoStock.id;
          }
        });
      }
    });
    this.reset();
    window.scrollTo(0, 0);
    this.ticketsService.setCompra(this.compra).subscribe(response => {
      this.messageSuccess = 'Compra registrada exitosamente';
      this.clickedCreat = false;
      this.compra = undefined;

      this.datatable.pushData(response);
    }, error => this.displayErrors(error));
  }

  public updateTotalCompra() {
    this.compra.iva = 0;
    this.compra.total = 0;
    this.compra.subtotal = 0;
    this.compra.productos.forEach((producto) => {
        this.compra.iva += producto.iva ? producto.iva : 0;
        this.compra.total += producto.costo ? producto.costo : 0;
        this.compra.subtotal += producto.subtotal ? producto.subtotal : 0;
      }
    );
  }

  public seeProducts(event, dataTable) {
    this.datatable = dataTable;
    if (!this.productos) {
      this.ticketsService.getProductos().subscribe(response => {
        this.productos = response;
        this.dataService = this.completerService.local(this.productos, 'descripcion', 'descripcion');
      });
    }
    if (!this.listaProveedores) {
      this.listaProveedores = this.ticketsService.getNameAgentes();
    }
    this.ticketsService.getCompra(event).subscribe(response => {
      this.clickedCreat = true;
      this.inUpdateProducts = true;
      this.compra = response;
      console.warn(this.compra);
      console.warn(response);
    }, error => this.displayErrors(error));
  }

  public actualizarCompra() {
    this.reset();
    this.compra.productos.forEach(producto => {
      if (producto) {
        this.productos.forEach(productoStock => {
          if (producto.descripcion === productoStock.descripcion) {
            producto.id = productoStock.id;
          }
        });
      }
    });
    console.warn(this.compra);
    this.ticketsService.updateCompra(this.compra).subscribe(response => {
      this.datatable.updateData(response);
      this.messageSuccess = 'Compra actualizada correctamente';
      this.clickedCreat = false;
      this.compra = undefined;
      console.warn(response);
    }, error => this.displayErrors(error));
  }

}

