import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Ticket} from '../models/ticket';

@Injectable({
  providedIn: 'root'
})
export class TicketsService {

  constructor(private http: HttpClient) {
  }

  public getTicket(): Observable<Ticket[]> {
    return this.http.get<Ticket[]>('http://api.vidamarinapuebla.com/api/tickets');
  }

  public updateTicket(ticket: Ticket): Observable<Ticket> {
    return this.http.put<Ticket>('http://api.vidamarinapuebla.com/api/tickets/' + ticket.id, ticket);
  }

  public createTicket(ticket: Ticket): Observable<Ticket> {
    return this.http.post<Ticket>('http://api.vidamarinapuebla.com/api/tickets', ticket);
  }

  public deleteTicket(ticket: Ticket): Observable<Ticket> {
    return this.http.delete<Ticket>('http://api.vidamarinapuebla.com/api/tickets/' + ticket.id);
  }

  public getNameAgentes(): Observable<any> {
    return this.http.get('http://api.vidamarinapuebla.com/api/tickets/nombresAgentes');
  }

  public getProductos(): Observable<any> {
    return this.http.get('http://api.vidamarinapuebla.com/api/tickets/productosForAdd');
  }

  public setCompra(producto): Observable<any> {
    return this.http.post('http://api.vidamarinapuebla.com/api/tickets/setProductos', producto);
  }

  public getCompra(id): Observable<any> {
    return this.http.get('http://api.vidamarinapuebla.com/api/tickets/getCompra/' + id);
  }

  public updateCompra(ticket): Observable<any> {
    return this.http.post('http://api.vidamarinapuebla.com/api/tickets/updateCompra', ticket);
  }
}
